import images from './connectResource'

const mainFolder = 'assets'

const svgIconPlaceholder = document.querySelectorAll('.svg-placeholder')

let iconName
let fullIconUrl
let MinifiedIconUrl
let IconUrlIndex
let icon

const getFullIconUrl = (iconName) => {
    return images[`./${iconName}.svg`]
}

const getMinifiedIconUrl = (mainFolder, fullIconUrl) => {
    IconUrlIndex = fullIconUrl.indexOf(mainFolder)
    return fullIconUrl.slice(IconUrlIndex)
}

svgIconPlaceholder.forEach((item) => {
    iconName = item.getAttribute('data-icon')
    fullIconUrl = getFullIconUrl(iconName)
    MinifiedIconUrl = getMinifiedIconUrl(mainFolder, fullIconUrl)
    icon = `<object data="${MinifiedIconUrl}" type="image/svg+xml"></object>`
    item.innerHTML = icon
})