import images from './connectResource'

const mainFolder = 'assets'

const imgPlaceholder = document.querySelectorAll('.img-placeholder')

let imageName
let imageExtension
let fullImageUrl
let MinifiedImageUrl
let imageUrlIndex
let img

const getFullImageUrl = (imageName, imageExtension) => {
    return images[`./${imageName}.${imageExtension}`]
}

const getMinifiedImageUrl = (mainFolder, fullImageUrl) => {
    imageUrlIndex = fullImageUrl.indexOf(mainFolder)
    return fullImageUrl.slice(imageUrlIndex)
}

imgPlaceholder.forEach((item) => {
    imageName = item.getAttribute('data-img')
    imageExtension = item.getAttribute('data-extension')
    fullImageUrl = getFullImageUrl(imageName, imageExtension)
    MinifiedImageUrl = getMinifiedImageUrl(mainFolder, fullImageUrl)
    img = `<img src="${MinifiedImageUrl}" alt="${imageName}">`
    item.innerHTML = img
})